import Vue from 'vue';
import Router from 'vue-router';
import Main from '../components/Main';

import AlertPage from '../components/AlertPage';
import MessPage from '../components/MessPage';
import DesCrip from "../components/DesCrip";
import DesModel from "../components/DesModel";
import MessModel from "../components/MessModel";
import WorkPage from "../components/WorkPage";
import Login from "../components/login";
import Person from "../components/Person";
import Company from "../components/Company";
import Register from "../components/Registers";
import TelDes from "../components/TelDes";
import TelAdd from "../components/TelAdd";
import AddWork from "../components/AddWork";
import AddMore from "../components/AddMore";
import ResWork from "../components/ResWork";
import Project from "../components/Project";
import ProjectItem from "../components/ProjectItem";
import Machine from "../components/Machine";
import MachineItem from "../components/MachineItem";
import NewMachine from "../components/NewMachine";
import MoreUse from "../components/MoreUse";
import WorkModel from "../components/WorkModel";
import SubWork from "../components/SubWork";
import DesProject from "../components/DesProject";
import NewProject from "../components/NewProject";
import ProMap from "../components/ProMap";
import Spread from "../components/Spread";
import DocMess from "../components/DocMess";
import CheckMachine from "../components/CheckMachine";


Vue.use(Router)

var router = new Router({
	routes: [{
			path: '/',
			component: Login
		},
		//登录
		{
			path: '/login',
			component: Login

		},
		//注册
		{
			path: '/register',
			component: Register

		},
		//个人注册
		{
			path: '/person',
			component: Person

		},
		//单位注册
		{
			path: '/company',
			component: Company

		},
		//主页

		{
			path: '/main',
			component: Main

		},
		//报警提醒
		{
			path: "/alertpage",
			component: AlertPage

		},
		
		//报警列表
		{

			path: "/desCrip/:cid/:model",
			component: DesCrip
		},
		//报警详情
		{

			path: "/desmodel/:cid/:tit/:time",
			component: DesModel
		},
		//信息提醒
		{
			path: '/messPage',
			component: MessPage
		},
		
		//信息详情
		{

			path: "/read/:cid/:tit",
			component: MessModel
		},
		//  我的任务
		{

			path: "/WorkPage",
			component: WorkPage
		},
		//任务详情
		{

			path: "/workmodel/:id",
			component: WorkModel
		},
		//任务填报 
		{

			path: "/subwork",
			component: SubWork
		},
		//添加任务
		{

			path: "/addwork",
			component: AddWork
		},
		{

			path: "/addmore",
			component: AddMore
		},
		{

			path: "/reswork",
			component: ResWork
		},
		//工程列表
		{

			path: "/project",
			component: Project
		},
		//工程详情
		{

			path: "/desproject/:order",
			component: DesProject
		},
		//新增工程
		{

			path: "/newproject",
			component: NewProject
		},
		//工程定位
		{

			path: "/promap",
			component: ProMap
		},
		{

			path: "/proitem/:order/:ord_num",
			component: ProjectItem
		},
		//设备信息
		{

			path: "/machine",
			component: Machine
		},
		//设备详情
		{

			path: "/machine/:id",
			component: MachineItem
		},
		//  设备分布
		{

			path: "/spread",
			component: Spread
		},
		//新增设备
		{

			path: "/newmachine",
			component: NewMachine
		},
		//设备报修
		{

			path: "/checkmachine/:id",
			component: CheckMachine
		},
		{

			path: "/moreUse",
			component: MoreUse
		},
		//档案信息
		{

			path: "/docmess",
			component: DocMess
		},
		//通讯录详情页
		{

//			path: "/teldes/:cid",
			path: "/teldes",			
			component: TelDes
		},
		//通讯录添加
		{

//			path: "/teldes/:cid",
			path: "/teladd",			
			component: TelAdd
		}

	]
});

export default router;


